.body-items {
  background: url(../images/bg-report.png) no-repeat;
  background-size: cover;
  background-position: top;
  height: 100vh;
  .contenedor-search {
    background-size: cover;
    padding: 100px 2rem 0 2rem;
    border-radius: 0 0 35px 35px;
    .search {
      display: flex;
      align-items: center;
      position: relative;
      .icon-search {
        position: absolute;
        right: 0;
        transform: translateX(-140%);
        width: 6%
      }
      .form-control {
        background: #fff;
        line-height: 2;
        border-color: transparent;
        border-radius: .7rem;
        font-size: .8rem;
        // margin-right: .7rem;
        padding: .6rem .8rem;
        &:focus {
          background: #fff;
          border-color: transparent;
          box-shadow: 0 0 0 2px rgb(194, 206, 202);
          color: #EB8950;
        }
      }
    }
  }
  .contenedor {
    padding-bottom: 70px;
    .contenedor-items {
      margin: 0 1.3rem;
      padding: 1rem;
      background-color: #FFFFFF;
      border-radius: .7rem;
      a {
        text-decoration: none;
      }
      .items {
        display: flex;
        align-items: center;
        img {
          width: 15%;
        }
        .text-items {
          width: 100%;
          text-align: center;
          .title-items {
            margin-bottom: 0;
            padding-left: .5rem;
            font-size: 1.2rem;
            color: #E67330;
          }
        }
      }
    }
  }
}
.search input::placeholder {
  color: rgb(172, 166, 166);
}